// Grid

.main {
  @include make-col-ready();
  @include make-col(12);

  .sidebar-primary & {
    @include media-breakpoint-up(lg) {
      @include make-col(9);
    }
  }
}

.sidebar {
  @include make-col-ready();

  @include media-breakpoint-up(lg) {
    @include make-col(3);
  }
}



